import { AxiosError, type AxiosResponse } from 'axios'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { Navigate, useNavigate, useParams } from 'react-router'

import type { SelectChangeEvent } from '@mui/material'
import { Box, Button, Card, Container, Divider, Grid, Tab, Tabs, Typography } from '@mui/material'

import { AUTH_USER_MESSAGE, DATE_MESSAGE, WITHDRAWAL_USER_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, FIRST_SEMESTER_NAME, SECOND_SEMESTER_NAME } from 'src/data/semester'

import { deleteClubWithdrawal } from 'src/api/withdrawalClub'

import type { ClubMemberForAuth } from 'src/models/ClubMember'
import type { DateProps, Semester } from 'src/models/date'
import { defaultDate } from 'src/models/date'
import { isSemester, isYear } from 'src/models/date'
import { type Profile } from 'src/models/profile'
import type { User } from 'src/models/user'

import ConfirmDialog from 'src/components/ConfirmDialog'
import DateForm from 'src/components/DateForm'
import ErrorMessageBox from 'src/components/ErrorMessageBox'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import useUpdateAuthState from 'src/hooks'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

import ConferenceTable from './ConferenceTable'
import UserProfile from './UserProfile'

interface TabPanelProps {
  children?: React.ReactNode
  index: number
  value: number
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  )
}

const defaultUserInfo: User = {
  id: -1,
  name: '',
  studentId: '',
  clubs: []
}

const ProfilePage: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)
  const [profile, setProfile] = useState<Profile>()
  const [isDialog, setIsDialog] = useState<boolean>(false)
  const [tabIndex, setTabIndex] = useState<number>(0)
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const navigate = useNavigate()
  const { userInfo, isLoggedIn } = useSafeAuthUser()
  const updateAuthState = useUpdateAuthState()

  const club = userInfo?.clubs.find(c => c.id === clubId) as ClubMemberForAuth
  const clubDate: DateProps = { year: club?.year || defaultDate.year, semester: club?.semester || defaultDate.semester }
  const [date, setDate] = useState<DateProps>({
    year: clubDate.year,
    semester: clubDate.semester
  })

  const handleDateChange = ({ target: { value } }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    if (isSemester(value)) {
      setDate(prevDate => ({ ...prevDate, semester: value as Semester }))
      return
    }
    if (isYear(value)) {
      setDate(prevDate => ({ ...prevDate, year: Number(value) }))
      return
    }
    enqueueSnackbar(DATE_MESSAGE.INVALID_DATE, { variant: 'warning' })
  }

  const handleDelete = async () => {
    try {
      await deleteClubWithdrawal({ clubId, token: authHeader() })

      updateAuthState(
        userInfo ?? defaultUserInfo,
        userInfo?.clubs.filter(c => c.id !== clubId) ?? defaultUserInfo.clubs
      )

      enqueueSnackbar(WITHDRAWAL_USER_MESSAGE.WITHDRAWAL_SUCCESS, { variant: 'success' })
      navigate(`/club/${clubId}`)
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err?.response?.data?.message ?? WITHDRAWAL_USER_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      } else {
        enqueueSnackbar(WITHDRAWAL_USER_MESSAGE.UNKNOWN_ERROR, {
          variant: 'error'
        })
      }

      setIsDialog(false)
    }
  }

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabIndex(newValue)
  }

  useEffect(() => {
    if (!club) {
      navigate(`/club/${clubId}`)
    }
    axios
      .get(`${process.env.REACT_APP_APIHOST}/member/${clubId}/status?semester=${date.semester}&year=${date.year}`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Profile>) => {
        setProfile(res.data)
      })
      .catch(err => {
        enqueueSnackbar(`${err.response?.data?.message}`, {
          variant: 'error'
        })
        navigate(`/club/${clubId}`)
      })
  }, [date])

  if (isLoggedIn && !userInfo) {
    enqueueSnackbar(AUTH_USER_MESSAGE.WRONG_AUTH_STATE, { variant: 'error' })
    return <Navigate to="/" />
  }

  return (
    <>
      <Helmet>
        <title>프로필</title>
      </Helmet>
      {club && (
        <PageTitleWrapper>
          <Grid container alignItems="center">
            <Grid item>
              <Typography variant="h3" component="h3" gutterBottom>
                {club.name}
              </Typography>
              <Typography variant="subtitle2">
                {userInfo?.nickname} 님이 {club.name}에서 활동한 내역입니다
              </Typography>
            </Grid>
          </Grid>
        </PageTitleWrapper>
      )}
      <Container maxWidth="lg">
        <DateForm clubDate={clubDate} date={date} handleDateChange={handleDateChange} />

        {profile ? (
          <Card>
            <Tabs value={tabIndex} onChange={handleTabChange} sx={{ margin: 2 }}>
              <Tab label="내 정보" />
              <Tab label="출석" />
            </Tabs>
            <Divider />
            <TabPanel value={tabIndex} index={0}>
              <UserProfile profile={profile} />
            </TabPanel>
            <TabPanel value={tabIndex} index={1}>
              <ConferenceTable conferences={profile.conferences} />
            </TabPanel>
          </Card>
        ) : (
          <ErrorMessageBox
            children={`${date.year}년도 ${
              date.semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
            }에는 존재하는 활동내역이 없습니다. 다른학기를 선택해 주세요.`}
          />
        )}
      </Container>
      <Box textAlign="center" mt={3}>
        <Button
          variant="contained"
          color="error"
          onClick={() => {
            setIsDialog(true)
          }}
        >
          동아리 탈퇴
        </Button>
      </Box>
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        handleSubmit={handleDelete}
      />
    </>
  )
}

export default ProfilePage
